<template>
  <div class="login-page">
    <div class="login-header">
      <img src="../../assets/img/cactus.png" alt="" />
    </div>
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="username"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div class="bottons" style="margin: 16px">
        <van-button round plain type="primary" native-type="submit">
          提交
        </van-button>
        <van-button round plain type="warning" to="/register">
          注册
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { loginApiFun } from './api'
export default {
  data() {
    return {
      username: '',
      password: '',
    }
  },
  methods: {
    onSubmit(values) {
      loginApiFun(values)
      console.log('submit', values)
    },
  },
}
</script>

<style lang="scss" scoped>
.login-page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  .login-header {
    width: 100%;
    text-align: center;
  }
  .bottons {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
}
</style>
